.App {
  text-align: center;
  padding: 20px;
  color: #fff;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.app-container {
	width: 90%;
	background-color: #282c34;
	border-radius: 10px;
}
.tablerow.header {
	background-color: #342833;
	color: #fff;
}
.tablerow.header>div {
	background-color: transparent;
}
.action-button {
	margin: 10px;
	filter: unset;
	border: 1px solid #70206a;
}
input {
	margin: 10px;
}
.card-content.data-table {
	height: 70vh;
	background-color: #34282885;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
